#pre-header {
  #block-search-form {
    // padding-top: 1em;
    max-width: 250px;
    padding-bottom: 0;
    @media (min-width: @screen-xs-min) {
    }
    .form-search {
      #edit-search-block-form--2 {
        // display: inline-block;
        background-color: #3A3A3A;
        color: #CCC;
        border: 1px solid #444;
        // width: 200px;
        min-height: 20px;
        height: 20px;
        line-height: 20px;
        padding: 5px 10px;
        border-radius: 4px 0 0 4px;
        box-sizing: content-box;
        // @media (max-width: 424px) {
        //   width: calc(100% - 60px);
        // }
      }
      .input-group {
        // width: 100%;
      }
      .input-group-btn {
        // display: inline-block;
      }
      .form-actions {
        margin-bottom: 0;
      }
      button {
        background-color: @main-color;
        border: none;
      }
    }
  }
}

#search-form {
  .form-text {
    display: inline-block;
    width: 300px;
    height: 32px;
    min-height: 32px;
    line-height: 20px;
    padding: 5px 10px;
    &:focus {
      border-color: @main-color;
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px fadeout(@main-color, 40%);
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px fadeout(@main-color, 40%);
    }
  }
  .input-group-btn {
    display: inline-block;
    button {
      background-color: @main-color;
      border: none;
    }
  }
}

.page-search {
  #block-system-main {
    h2 {
      display: none;
    }
    .search-results {
      padding-left: 0;
      li {
        list-style-type: none;
        border: 1px solid #EEE;
        padding: 15px;
        margin-bottom: 15px;
        h3 {
          font-weight: normal;
          margin-top: 0;
          a:hover {
            text-decoration: none;
            color: darken(@main-color, 30%);
          }
        }
        .search-snippet {
          color: #888;
          font-size: 16px;
          margin-top: 20px;
          margin-bottom: 20px;
        }
        .more-link {
          .black-more-link;
          text-align: left;
          a {
            margin-top: 0;
          }
        }
      }
    }
  }
}